<template>
  <v-app>
    <v-navigation-drawer v-model="value" absolute hide-overlay >
      <v-list dense nav shaped>
        <v-list-item-group>
          <router-link to="/user/profile">
            <v-list-item>
              <v-list-item-icon>
                <v-icon size="20">mdi-badge-account</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  Profile Info
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </router-link>
        </v-list-item-group>
      </v-list>
      <v-list dense nav shaped>
        <v-list-item-group>
          <router-link to="/user/wishlist">
            <v-list-item>
              <v-list-item-icon>
                <v-icon size="20">mdi-cards-heart</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  Wishlist
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </router-link>
        </v-list-item-group>
      </v-list>
      <v-list dense nav shaped>
        <v-list-item-group>
          <router-link to="/user/notification">
            <v-list-item>
              <v-list-item-icon>
                <v-icon size="20">mdi-bell-badge</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  Notification
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </router-link>
        </v-list-item-group>
      </v-list>
      <v-list dense nav shaped>
        <v-list-item-group>
          <router-link to="/user/reviews">
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-file-star</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  Reviews
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </router-link>
        </v-list-item-group>
      </v-list>
      <v-list dense nav shaped>
        <v-list-item-group>
          <a href="https://play.google.com/store/apps/details?id=com.bizlxapp" target="_blank">
            <v-list-item>
              <v-list-item-icon>
                <v-icon>mdi-progress-download</v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  Download App
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </a>
        </v-list-item-group>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar app color="error" class="changes" elevation="1">
      <v-app-bar-nav-icon color="white" @click="toggleValue" class="ms-md-2"></v-app-bar-nav-icon>
      <v-spacer></v-spacer>
<!--      <p class="text-white h5 me-1">Bizlx</p>-->
      <v-menu offset-y>
        <template v-slot:activator="{ on, attrs }">
          <v-avatar v-if="userdatas.user_avatar !== null">
            <img :src=(url+userdatas.user_avatar) v-bind="attrs" v-on="on">
          </v-avatar>
          <v-avatar v-else color="blue">
            <span class="white--text fw-bold text-h5" v-bind="attrs" v-on="on">{{userdatas.name.charAt(0)}}{{firstCharOfLastWord}}</span>
          </v-avatar>
        </template>
        <b-list-group>
          <b-list-group-item>{{ userdata.email }}</b-list-group-item>
          <router-link to="/user/profile"><b-list-group-item variant="danger">Profile</b-list-group-item></router-link>
          <b-list-group-item @click="userLogout">Logout</b-list-group-item>
        </b-list-group>
      </v-menu>
    </v-app-bar>
    <v-main class="love p-2">
      <div class="container-fluid">
        <router-view></router-view>
      </div>
    </v-main>
  </v-app>
</template>

<script>
import $ from "jquery";
import axios from "axios";

export default {
  name: "UserSidebar",
  data () {
    return {
      collapseOnScroll: true,
      value: true,
      userdata: [],
      userdatas: [],
      url:'https://bizlx.itechvision.in',
      // url:'http://127.0.0.1:8000',
    }
  },
  created() {
    this.getuserdata();
    this.isAuthenticated();
    this.buser_data();
    this.userdata = this.buser_data();
    this.user_role();
    if(this.isAuthenticated() == true && this.user_role() == 0){
      console.log('its ok');
      // this.$router.push({name: 'usersidebar'});
    }else {
      this.$router.push({name: 'userlogin'});
      console.log('not ok');

    }
  },
  computed: {
    lastWord() {
      const words = this.userdatas.name.split(' ');
      return words[words.length - 1];
    },
    firstCharOfLastWord() {
      return this.lastWord.charAt(0);
    }
  },
  methods: {
    getuserdata(){
      axios.get('userdata')
          .then((resp) =>{
            this.userdatas = resp.data.user;
          })
    },
    userLogout() {
      this.$store.dispatch('logout');
      this.$router.push('/user/login');
    },
    isAuthenticated() {
      return this.$store.state.isAuthenticated;
    },
    user_role() {
      return this.$store.state.userData.user_role;
    },
    buser_data() {
      return this.$store.state.userData;
    },
    toggleValue() {
      this.value = !this.value; // Toggle the value between true and false
    },
  },
  watch: {
    value(newValue) {
      console.log('Value changed:', newValue);
    },
  },
}
$(document).ready(function(){
  $(".v-app-bar__nav-icon").click(function(){
    $("main").toggleClass("love");
  });
});
</script>

<style scoped>
.love{
  padding: 10px 0 0 260px !important;
  }
.admin + div .footer {display: none;}
@media (min-width: 577px) {
  .bdash > div{
    height: calc(100vH - -50px);
    top: 155px;
    /*width: 240px;*/
    overflow: auto;
    transform: translateX(0%);
    position: fixed;
    z-index: 2;
  }
  .bdash .v-list{
    padding: 5px 0 !important;
    }
  .uright {
    max-width: calc(100vw - 157px - calc(var(--bs-gutter-x) * 0.5));
    width: 100%;
    left: 157px;
    position: absolute;
  }
  .uleft{
    max-width: 157px;
    min-width: 157px;
  }
}
@media (max-width: 576px) {
  .love {
    padding: 0px !important;
    }
  .bdash > div{
    height: calc(100vH - -50px);
    top: 121px;
    width: 60px;
    overflow: auto;
    transform: translateX(0%);
    position: fixed;
  }
  .uright{
    max-width: calc(100vw - 60px);
    width: 100%;
  }
  .uleft{
    max-width: 60px;
    min-width: 60px;
  }
}
</style>